.exploreIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 48px;
    height: 48px;
    background-color: #36393f;
    color: #43b581;
    transition: all 0.2s ease;
    margin: 8px 0;
}

.exploreIcon:hover {
    border-radius: 30%;
    background-color: #43b581;
    color: white;
}

.exploreIcon_icon {
    font-size: 24px;
}